@import "../../../color.scss";

.radio_wrapper {
    display: flex;
    flex-wrap: wrap;
    column-gap: .5rem;
    row-gap: .5rem;
}

.radio_component {
    display: flex;
    align-items: center;
    input {
        display: none;
        width: 1rem;
        margin-right: .5rem;
        filter: none;
        &:checked + label {
            border: 2px solid $blue;
            background-color: $blue;
            color: $white;
        }
    }
    label {
        padding: .5rem .7rem;
        border-radius: .5rem;
        border: 2px solid lighten($blue, 20);
        color: $blue;
        font-weight: 500;
        height: min-content;
        transition: .2s;
    }
}


.input_timer:disabled {
    background-color: $white;
}

.stop-watch {
    text-align: center;
    + form {
        margin-top: 1.5rem;
        .one-to-one_form {
            margin-top: 0;
        }
    }
}

.timer {
    padding-right: 10px;
    font-family: din-2014, ryo-gothic-plusn, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-style: italic;
    font-size: 2.5rem;
    color: mix($black, $grey);
}

.Control-Buttons {
    display: flex;
    gap: .5rem;
    justify-content: center;
    button {
        font-family: din-2014, ryo-gothic-plusn, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
        font-size: 1.1rem;
        letter-spacing: .05rem;
        background-color: $white;
        border: none;
        width: 5rem;
        height: 3rem;
        font-weight: 500;
        filter: drop-shadow(0 1px 2px rgba($grey, .5));
        border-radius: 10px;
        color: $white;

        &[name="start"] {
            background-color: $white;
            border: 2px solid $red;
            color: $red;
        }

        &[name="end"] {
            background-color: $red;
        }

        &.reset {
            background-color: $white;
            border: 2px solid $grey;
            color: $grey;
            // background-color: $grey;
        }
    }
}